import axios from 'axios';

export default {
  data() {
    return {
      name: '',
      address: '',
      mail: '',
    };
  },
  methods: {
    submitForm() {
      axios.post('/api/check-user-data', {
        name: this.name,
        address: this.address,
        email: this.mail,
      })
        .then(response => {
          console.log(response);
        })
        .catch(error => {
          console.log(error);
        });
    },
  },
};